import React from "react";
import Languages from "./languages";
import react from "./assets/react.svg";
import angular from "./assets/angular.svg";
import python from "./assets/python.svg";
import java from "./assets/java.svg";
import node from "./assets/node.svg";
import c from "./assets/c.svg";
import html from "./assets/html.svg";
import css from "./assets/css.svg";
import js from "./assets/javascript.svg";
const LanguageSection = () => {
  return (
    <div className="w-full flex justify-center items-center">
      <div className="w-2/3 flex flex-col flex-wrap gap-10 items-center">
        <p className="text-3xl">General Programming</p>
        <div className="flex flex-row flex-wrap gap-5 justify-evenly">
          <Languages
            value={70}
            language="Python"
            languageLogo={python}
            color="#387eb8"
          />
          <Languages
            value={70}
            language="Java"
            languageLogo={java}
            color="#E76F00"
          />
          <Languages value={60} language="C" languageLogo={c} color="#659ad2" />
        </div>
        <p className="text-3xl">Web</p>
        <div className="flex flex-row flex-wrap gap-5 justify-evenly">
          <Languages
            value={75}
            language="HTML"
            languageLogo={html}
            color="#e44d26"
          />
          <Languages
            value={65}
            language="CSS"
            languageLogo={css}
            color="#2965f1"
          />
          <Languages
            value={70}
            language="JavaScript"
            languageLogo={js}
            color="#f7df1e"
          />
          <Languages
            value={60}
            language="Node"
            languageLogo={node}
            color="#619857"
          />
        </div>
        <p className="text-3xl">Frameworks</p>
        <div className="flex flex-row flex-wrap gap-5 justify-evenly">
          <Languages
            value={60}
            language="React"
            languageLogo={react}
            color="#61DAFB"
          />
          <Languages
            value={50}
            language="Angular"
            languageLogo={angular}
            color="#DD0031"
          />
        </div>
      </div>
    </div>
  );
};

export default LanguageSection;
