import React from "react";
import background from "./assets/temp_background.jpg";
import linkedin from "./assets/linkedin.svg";
import gmail from "./assets/gmail.svg";
import github from "./assets/github.svg";
import instagram from "./assets/instagram.svg";

const Home = () => {
  return (
    <div className="h-screen relative flex flex-col items-center">
      <div
        className="relative w-screen h-screen bg-cover bg-top bg-no-repeat opacity-25"
        style={{ backgroundImage: `url(${background})` }}
      ></div>
      <div className="absolute top-2/3 w-1/3 rounded-md flex flex-row justify-evenly items-center py-2 bg-yellow-100 bg-opacity-25 transform duration-500 hover:scale-110">
        <a
          href="https://www.linkedin.com/in/dz365"
          target="_blank"
          rel="noreferrer"
        >
          <img
            src={linkedin}
            className="w-16 transform duration-500 hover:scale-125"
            alt="LinkedIn"
          />
        </a>
        <a href="https://www.github.com/dz365" target="_blank" rel="noreferrer">
          <img
            src={github}
            className="w-16 transform duration-500 hover:scale-125"
            alt="GitHub"
          />
        </a>
        <a href="mailto:dwqz365@gmail.com" target="_blank" rel="noreferrer">
          <img
            src={gmail}
            className="w-16 transform duration-500 hover:scale-125"
            alt="Email"
          />
        </a>
        <a
          href="https://www.instagram.com/dz.2372"
          target="_blank"
          rel="noreferrer"
        >
          <img
            src={instagram}
            className="w-16 transform duration-500 hover:scale-125"
            alt="Instagram"
          />
        </a>
      </div>
    </div>
  );
};

export default Home;
